.nav {
    display: flex;
    justify-content: space-between;
    position: relative;
}
.item {
    display: flex;
    align-items: center;
    height: 56px;
    margin: 16px 20px;

    font-family: 'Jet Brains Mono';
    font-size: 16px;
    line-height: 1.5;
    font-weight: normal;
    color: #8585AD;
}
.header {
    height: 88px;
}
.icon {
    margin-right: 9px;
}
.logo {
    position: absolute;
    left: calc(50% - 145px);
    top: calc(50% - 25px);
}
.two {
    display: flex;
    gap: 8px;
}
